body {
  background-color: #121212;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}

.App {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 20px;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.App-header {
  grid-column: 1 / -1;
  background-color: #1e1e1e;
  padding: 30px;
  text-align: center;
  border-bottom: 1px solid #333;
}

.App-header h1 {
  font-size: 2.5em;
  margin-bottom: 15px;
  color: #eee;
}

section {
  background-color: #2d2d2d;
  padding: 20px;
  border-radius: 5px;
}

section h2 {
  font-size: 1.8em;
  margin-bottom: 15px;
  color: #eee;
}

section p {
  font-size: 1.1em;
  line-height: 1.6;
  color: #ddd;
}

input[type="text"] {
  padding: 12px;
  font-size: 1.1em;
  border: 1px solid #555;
  border-radius: 5px;
  background-color: #333;
  color: #fff;
  width: 100%;
  margin-bottom: 15px;
}

button {
  padding: 12px 25px;
  font-size: 1.1em;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

.heatmap-container {
  width: 100%;
  overflow-x: auto;
}
